<template>
  <div class="login-input-container">
    <div class="login-input-wrap">
      <img class="left-img" :src="leftImg"/>
      <input :placeholder="placeHolder" @change="inputChange"/>
      <div class="right-img" @click="clickRightBtn">
        <span>{{rightText}}</span>
        <img v-show="rightImg" :src="rightImg"/>
      </div>
    </div>
    <div class="border-line">
    </div>
  </div>

</template>

<script>
  export default {
    name: 'login-input',
    props: ['leftImg', 'rightImg', 'placeHolder', 'rightText'],
    data() {
      return {

      };
    },
    methods: {
      inputChange(e) {
        this.$emit('changeValue', e.target.value);
      },
      clickRightBtn() {
        this.$emit('rightHandle');
      },
    },
  };
</script>

<style lang="less" scoped>
  @import "../../style/common";
  .login-input-container {
    position: relative;
  }
  .login-input-wrap {
    position: relative;
    .wh(6.06,0.88);
    .flx-x-between;

    .left-img {
      .wh(0.88,0.88);

    }
    input {
      .wh(3,0.88);
    }
    .right-img {
      .wh(2,0.88);
      img {
        float: right;
        .wh(0.88,0.88);
      }
      span {
        float: right;
        text-align: right;
        .text-color4;
        .fontSize-four;
        .height(0.88);
        .line-height(0.88);
      }
    }
  }
  .border-line {
    border-bottom-color: #45D4FB;
  }



</style>



