import config from './config';

export const navigateTo = (url, routeName, query, vm) => {
  if (query !== {}) {
    // let str = '?';
    // for (let key in query) {
    //   str = str + `${key}=${query[key]}&`;
    // }
    // if (str.length > 1) {
    //   url = url + str.substring(0, str.length - 1);
    // }
    vm.$router.push({ name: routeName, query: query });
  } else {
    vm.$router.push({ name: routeName });
  }

  // if (config.isMpVue) {
  //   console.log(url)
  //   wx.navigateTo({
  //     url:url,
  //   });
  // } else {
  //   vm.$router.push({ path: routeName, query: query});
  // }
};

export const navgationBack = (delta, vm) => {
  if (config.isMpVue) {
    wx.navigateBack({
      delta: delta
    });
  } else {
    console.log('go')
    // vm.$router.go(delta);
    window.history.go(delta);
  }
};

export const switchTab = url => {
  wx.switchTab({
    url: url
  });
};



