<template>
  <div>
    <div :key=index v-for="(childTrend,index) in childTrendData">
      <div class="child-trend-view">
        <div class="userInfoHeadView">
          <userInfoHeadView :user-img="userImage"  user-name="甘宝宝的妈妈" from-info="北京蓝天幼儿园"></userInfoHeadView>
        </div>
        <div class="child-trend-content-view">
          <div class="trend-content">{{info}}</div>
          <trendImgPreview :image-list="imageList"></trendImgPreview>
          <trendBottomView time="今天 13:26" lick-count="2314" discuss-count="2365"></trendBottomView>
          <commentView></commentView>
        </div>
        <div class="border-line"></div>
      </div>
    </div>
  </div>

</template>

<script>

  import userInfoHeadView from './userInfoHeadView';
  import trendImgPreview from './trendImgPreview';
  import trendBottomView from './trendBottomView';
  import commentView from './commentView';

  export default {
    name: 'trend-preview',
    data() {
      return {
        info: '首届足球嘉年华开始啦！阳光之下显本色，宝宝们热情高涨首届足球嘉年华开始啦！阳光之下显本色，宝宝们热情高涨高高兴阳光之下显本色高高兴阳光之下显本色',
        index: 22,
        childTrendData: [{}, {}],
        liveTrendData: [{}],
        imageList: [1, 2, 3, 4, 5],
        img1: require('../../assets/index/index_circle_icon_disabled.png'),
        img2: require('../../assets/index/index_play_icon_disabled.png'),
        userImage: 'http://imgsrc.baidu.com/forum/w=580/sign=6ade238c064f78f0800b9afb49300a83/6cbb60d0f703918f31df8012543d269758eec47c.jpg',
      };
    },
    components: { userInfoHeadView, trendImgPreview, trendBottomView, commentView },
  };
</script>

<style lang="less" scoped>
  @import "../../style/common";
  .child-trend-view {
    position: relative;
  .width(7.1);
  .padding(0,0.2);
  }
  .child-trend-content-view {
  .width(6.9);
  .padding-left(0.2);
  }
  .userInfoHeadView {
  .margin-top(0.16);
  }
  .trend-content {
    color: @text-color1;
  .fontSize-three;
  .width(6.7);
  .margin(0.16,0);
  .textLine(2,0.32);
  }

</style>



