<template>
  <div class="trendImgPreview-container">
    <div class="item-container" v-for="(item,index) in imageList">
      <img class="image-item" src="item"/>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'trend-img-preview',
    data() {
      return {};
    },
    props: ['imageList'],
  };
</script>

<style lang="less" scoped>
  @import "../../style/common";
  .trendImgPreview-container {
    position: relative;
    width: 100%;
    .flx-wrap;
  }
  .item-container {
    .wh(2.3,2.3);
  }
  .image-item {
    .wh(2.1,2.1);
    background-color: yellowgreen;
  }

</style>



