<template>
  <div class="userInfoHeadView-container">
    <img :src="userImg"/>
    <span class="user-name">{{userName}}</span>
    <span class="from-text">来自 </span>
    <span class="from-info">{{fromInfo}}</span>
  </div>
</template>

<script>
  export default {
    name: 'user-info-head-view',
    props: ['userImg', 'userName', 'fromInfo'],

  };
</script>

<style lang="less" scoped>
  @import "../../style/common";
  .userInfoHeadView-container {
    .wh(7.1,0.8);
    img {
      position: absolute;
      .wh(0.68,0.68);
      .borderRadius(0.34);
    }
  }
  .user-name{
    position: absolute;
    .text-color4;
    .margin-left(0.9);
    .fontSize-five;
  }
  .from-text {
    position: absolute;
    .text-color3;
    .margin-left(0.9);
    .fontSize-five;
    .margin-top(0.32);
  }
  .from-info {
    position: absolute;
    .text-color2;
    .margin-left(1.5);
    .fontSize-five;
    .margin-top(0.32);
  }

</style>



