<template>
  <div class="comment-view-bg">
    <div class="triangle"></div>
    <div class="comment-view-container">
      <section class="like-view">
        <img src=""/>
        <span class="user-name">张静妈妈，悠悠妈妈,lele妈妈，呱呱蛙...</span>
        <div class="border-line"></div>
      </section>
      <section class="comment-view">
        <div class="comment">
          <span class="user-name">李老师:</span>
          <span class="user-content">宝宝很活跃，玩的很开心！</span>
        </div>
        <div class="reply">
          <span class="user-name">甘宝宝妈妈</span>
          <span class="user-content">回复</span>
          <span class="user-name">李溜溜老师:</span>
          <span class="user-content">是的，就是asf这样子啦，我也不知道哦他s们嗨什么嫩恩，真实uzihdf睡觉啊</span>
        </div>
      </section>
    </div>
  </div>


</template>

<script>
  export default {
    name: 'comment-view',
  };
</script>

<style lang="less" scoped>
  @import "../../style/common";
  .comment-view-bg {
    position: relative;
    .padding-bottom(0.24);
  }
  .triangle {
      margin-left: 10px;
      width: 0;
      height: 0;
      border-left: 8.5px solid transparent;
      border-right: 8.5px solid transparent;
      border-bottom: 17px solid @bg-color2;
  }
  .comment-view-container {
    .margin-right(0.12);
    .width(6.7);
    .box-sizing-padding-wrap;
    background-color: @bg-color2;
    .fontSize-five;

    .like-view {
      position: relative;
      .height(0.5);
    }
    .comment-view {

    }
  }
  .comment,reply {
    .padding(0.12,0);
  }
  .user-name {
    .text-color4;
  }
  .user-content {
    .text-color1;
    text-align:justify;
    text-align-last:justify;
  }

</style>



