<template>
  <div class="trendBottomView-container">
    <div class="time-item">{{time}}</div>
    <div class="like-item">
      <img v-if="liked" src="../../assets/index/index_like_btn_selected.png"/>
      <img v-else src="../../assets/index/index_likebtn_default.png"/>
      <span>{{lickCount}}</span>
    </div>
    <div class="discuss-item">
      <img src="../../assets/index/index_comment_btn_default.png"/>
      <span>{{discussCount}}</span>
    </div>
  </div>

</template>

<script>
  export default {
    name: 'trend-bottom-view',
    props: ['liked', 'time', 'lickCount', 'discussCount'],
    method: {
      clickLikeBtn() {
        this.$emit('clickLikeBtn');
      },
      clickDiscussBtn() {
        this.$emit('clickLickBtn');
      },
    },
  };
</script>

<style lang="less" scoped>
  @import "../../style/common";
  .trendBottomView-container {
    .wh(6.7,0.6);
    .flx-x-around;
  }
  .time-item {
    .wh(4.7,0.6);
    .line-height(0.6);
    .fontSize-five;
    .text-color3;
  }
  .like-item {
    .wh(1,0.6);
    .flx-x-center;
    img{
      .wh(0.24,0.24)
    }
    span{
      .fontSize-five;
      .text-color3;
    };
  }
  .discuss-item {
    .wh(1,0.6);
    .flx-x-center;
    img{
      .wh(0.24,0.24)
    }
    span{
      .fontSize-five;
      .text-color3;
    }
  }

</style>



